div.screen
{
	height: 260px;
	width: 280px;
	padding: 0 20px;
	background-color: #b0b0b0;
	float:left;
	position: absolute;
	top: 110px;
	left: 19px;

	border-radius: 15px;
	-webkit-border-radius: 15px;
	-moz-border-radius: 15px;
	-o-border-radius: 15px;
}

div.screen:after
{
	content: "";
	border-top: 40px solid #b0b0b0;
	border-left: 40px solid #c00d0d;
	height: 0;

	position: absolute;
	bottom: 0;
	left: 0;
}

